<template>
  <div>
    <deng-long3></deng-long3>
    <div class="animate-box">
      <div class="baba">
        <div v-for="(item,index) in list" :key="index" @click="jumpDetail(item)"> <img :src="item.img"> </div>
      </div>
    </div>
  </div>
</template>

<script>
import { lists } from '@/utils/Picture1.js'
export default {
  data() {
    return {
      list: [],
    }
  },
  mounted() {
    this.list = lists
  },
  methods: {
    jumpDetail(item) {
      this.$router.push({
        name: 'Detail',
        query: {
          item
        }
      })
    },
  }
}
</script>

<style scoped>
.animate-box {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #000;
}
.baba {
  position: relative;
  width: 300px;
  height: 300px;
  transform-style: preserve-3d;
  animation: zuan 10s linear alternate infinite;
}
.baba div {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0.8;
}
.baba img {
  width: 100%;
  height: 100%;
}
.baba div:nth-child(1) {
  transform: translateZ(150px);
}
.baba div:nth-child(2) {
  transform: rotateY(180deg) translateZ(150px);
}
.baba div:nth-child(3) {
  transform: rotateY(-90deg) translateZ(150px);
}
.baba div:nth-child(4) {
  transform: rotateY(90deg) translateZ(150px);
}
.baba div:nth-child(5) {
  transform: rotateX(90deg) translateZ(150px);
}
.baba div:nth-child(6) {
  transform: rotateX(-90deg) translateZ(150px);
}
@keyframes zuan {
  0%,
  5% {
    transform: rotateY(90deg);
  }
  20%,
  25% {
    transform: rotateY(180deg);
  }
  40%,
  45% {
    transform: rotateY(270deg);
  }
  60%,
  65% {
    transform: rotateX(-90deg);
  }
  80%,
  85% {
    transform: rotateX(0deg);
  }
  95%,
  100% {
    transform: rotateX(90deg);
  }
}
</style>
